import React from "react";
import CheckBoxWithText from "Global/Components/CommonComponents/CheckBoxWithText/CheckBoxWithText";
import FileUploaderDropZone from "Global/Components/CommonComponents/FileUploaderDropDown/FileUploaderDropZone";
import MuiDatePicker from "Global/Components/UnEditableComponents/MuiDatePicker/MuiDatePicker";
import RadioSelectionGeneratorWithGroup from "Global/Components/Generators/RadioSelectionGeneratorWithGroup/RadioSelectionGeneratorWithGroup";
import { ImageOrFileRadioDataArray } from "./ImageOrFileRadioDataArray";
import CustomizableSelect from "Global/Components/CommonComponents/CustomizableSelect/CustomizableSelect";
import PersianSupportTextField from "Global/Components/UnEditableComponents/PersianSupportTextField/PersianSupportTextField";
import FindLangKey from "Global/Functions/FindLangKey/FindLangKey";

export const DocumentUploaderDataArray = (gatheredData, languageArray) => {
  const [hasExpireDate, setHasExpireDate] = React.useState(false);
  const DataArray = [
    {
      element: (
        <FileUploaderDropZone
          label={FindLangKey(
            "upload-oncall-documents-btn-document-select",
            languageArray
          )}
        />
      ),
      name: "base64Content",
      required: true,
    },
    {
      element: (
        <CheckBoxWithText
          label={FindLangKey(
            "general-public-check-select-expiry-date",
            languageArray
          )}
          StateLift={setHasExpireDate}
          getInitialValue={true}
        />
      ),
      name: "HasExpireDate",
      // required: true,
    },
    {
      element: (
        <MuiDatePicker
          Placeholder={FindLangKey(
            "general-public-check-select-expiry-date",
            languageArray
          )}
          Disabled={!hasExpireDate}
        />
      ),
      name: "expireDate",
      // required: true,
    },
    // {
    //   element: (
    //     <RadioSelectionGeneratorWithGroup
    //       ElementArray={ImageOrFileRadioDataArray}
    //       defaultValue={false}
    //       spacing="11em"
    //       indexPosition={1}
    //       radioRightMargin="-2em"
    //       getInitialValue={true}
    //     />
    //   ),
    //   name: "imageOrFile",
    //   // required: true,
    // },
    {
      element: (
        <CustomizableSelect
          SelectableDataArray={gatheredData}
          label={FindLangKey(
            "upload-oncall-documents-select-document-type-select",
            languageArray
          )}
          isSingle={true}
        />
      ),
      name: "idType",
      required: true,
    },
    {
      element: (
        <PersianSupportTextField
          label={FindLangKey("general-public-description", languageArray)}
          isPassword={false}
        />
      ),
      name: "deaription",
    },
  ];
  return DataArray;
};
