export const GeneratorProps = (
  FileOrPictureDataArray,
  setRadioValue,
  ShowOrUpload
) => {
  const props = {
    ElementArray: FileOrPictureDataArray,
    defaultValue: "picture",
    GetFinalRadioValue: setRadioValue,
    spacing: "11em",
    indexPosition: 1,
    radioRightMargin: "-2em",
    disabled: ShowOrUpload,
  };
  return props;
};
