import { Container, Stack } from "@mui/material";
import SubmitButton from "Global/Components/UnEditableComponents/SubmitButton/SubmitButton";
import useGatherDataFromApi from "Global/Hooks/useGatherDataFromApi";
import ReactHookFormGenerator from "Global/ReactHookFormGenerator/ReactHookFormGenerator";
import React from "react";
import { useForm } from "react-hook-form";
import { GetDocumentType } from "../../Adapters/GetDocumentType";
import { SubmitOnCallUpload } from "../../Functions/SubmitOnCallUpload";
import { DocumentUploaderDataArray } from "../../Variables/DocumentUploaderDataArray";
import { DocumentUploaderDefaultValues } from "../../Variables/DocumentUploaderDefaultValues";
import FindLangKey from "Global/Functions/FindLangKey/FindLangKey";

const DocumentUploader = ({ StateBank, languageArray }) => {
  const { control, formState, handleSubmit } = useForm({
    defaultValues: DocumentUploaderDefaultValues,
  });
  const { gatheredData } = useGatherDataFromApi(GetDocumentType);

  return (
    <React.Fragment>
      <Container>
        <form
          onSubmit={handleSubmit((data) => SubmitOnCallUpload(data, StateBank))}
        >
          <Stack gap=".5em">
            <ReactHookFormGenerator
              control={control}
              formState={formState}
              DataArray={DocumentUploaderDataArray(gatheredData, languageArray)}
            />
            <SubmitButton
              label={FindLangKey(
                "upload-oncall-documents-btn-send-documents",
                languageArray
              )}
            />
          </Stack>
        </form>
      </Container>
    </React.Fragment>
  );
};

export default DocumentUploader;
