export const ReturnItemValuePairs = (ApiArray, itemValue, itemContent) => {
  const resultArray = ApiArray?.map((element) => {
    return {
      ItemValue: element[itemValue],
      ItemContent:
        typeof itemContent === "function"
          ? itemContent(element)
          : typeof itemContent === "string"
          ? element[itemContent]
          : element,
    };
  });
  return resultArray;
};
