import {
  FormControl,
  FormControlLabel,
  Radio,
  RadioGroup,
  Stack,
  Typography,
} from "@mui/material";
import { GlobalContext } from "Global/Context/GlobalContextProvider";
import React, { useContext, useMemo, useState } from "react";
import { typeCheck } from "./Functions/RadioGroupTypeChecker";

const RadioSelectionGeneratorWithGroup = ({
  ElementArray,
  // eg. [
  //     {
  //         "RadioValue": "file",
  //         "RadioLabel": "فایل"
  //     },
  //     {
  //         "RadioValue": "picture",
  //         "RadioLabel": "تصویر"
  //     }
  // ]
  defaultValue,
  GetFinalRadioValue,
  onChange,
  small,
  spacing,
  indexPosition,
  radioRightMargin,
  disabled,
  getInitialValue,
  UiChangeFunction,
  onClickFunction,
  fontSize,
}) => {
  const [uiValue, setUiValue] = useState(defaultValue ?? "");
  const GlobalConstants = useContext(GlobalContext);
  React.useEffect(() => {
    GetFinalRadioValue && GetFinalRadioValue(uiValue);
    getInitialValue && onChange(uiValue);
  }, []);
  React.useEffect(() => {
    UiChangeFunction && UiChangeFunction();
  }, [uiValue]);
  const MemoFormControlLabel = useMemo(() => {
    return ElementArray?.map((element, index) => (
      <FormControlLabel
        dir="rtl"
        value={element.RadioValue ?? ""}
        control={
          <Radio
            disabled={disabled ?? false}
            color="colorPrimer"
            onClick={(event) => {
              if (onClickFunction) {
                onClickFunction(event.target);
              }
            }}
            sx={{
              fontSize: fontSize || "",
              p: 0,
              marginRight:
                !indexPosition || ElementArray.length === indexPosition
                  ? index === ElementArray.length - 1 && "-1em"
                  : index === indexPosition && radioRightMargin,
            }}
          />
        }
        label={
          small ? (
            <Typography variant="body2" color="textSecondary">
              {element.RadioLabel ?? ""}
            </Typography>
          ) : (
            <>{element.RadioLabel ?? ""}</>
          )
        }
        key={GlobalConstants.uuidV4()}
      />
    ));
  }, [ElementArray, GlobalConstants, small, disabled]);
  return (
    <>
      <FormControl sx={{ width: "100%" }}>
        <RadioGroup
          aria-labelledby="demo-radio-buttons-group-label"
          value={uiValue ?? ""}
          name="radio-buttons-group"
          row
          onChange={(e, val) => {
            setUiValue(val);
            GetFinalRadioValue && GetFinalRadioValue(val);
            onChange && onChange(typeCheck(val));
          }}
        >
          <Stack
            direction="row"
            alignItems="center"
            justifyContent="center"
            width="100%"
            gap={spacing ?? ".4em"}
          >
            {MemoFormControlLabel}
          </Stack>
        </RadioGroup>
      </FormControl>
    </>
  );
};

export default RadioSelectionGeneratorWithGroup;
