import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import Cookies from 'js-cookie';
import { Post } from 'Xhr/Xhr';
import { env } from 'env';

export const UploadDoctorDocument = async (
  data,
  isEditParam,
  StateBank
) => {
  const idPerson = JSON.parse(Cookies.get('user'));
  SwalAlert('info', 'در حال ارسال', false);
  Post(
    'POST',
    `${process.env.REACT_APP_API_URL_API_DOCTOR_ADD_DOCTOR_MADRAK}`,
    true,
    {
      idPerson: idPerson.IdPerson,
      id: isEditParam ? 1 : 0,
      ...data,
      nameType: '',
    }
  ).then((response) => {
    if (response.data.resCode === 1) {
      SwalAlert('success', response.data.resMessage, false);
    } else
      SwalAlert('error', response.data.resMessage, false);
    StateBank?.setForceRender((prev) => !prev);
  });
};
