import { Button, Container, Stack } from "@mui/material";
import RadioSelectionGeneratorWithGroup from "Global/Components/Generators/RadioSelectionGeneratorWithGroup/RadioSelectionGeneratorWithGroup";
import React from "react";
import ShowOrSendStateButton from "./components/ShowOrSendStateButton";
import { FileOrPictureDataArray } from "./variables/FileOrPictureDataArray";
import { GeneratorProps } from "./variables/GeneratorProps";

const FileOrPictureSelector = ({
  setShowOrUpload,
  ShowOrUpload,
  setRadioValue,
  StateBank,
  languageArray,
}) => {
  const props = GeneratorProps(
    FileOrPictureDataArray(languageArray),
    setRadioValue,
    ShowOrUpload
  );
  return (
    <React.Fragment>
      <Container>
        <Stack gap=".5em">
          <RadioSelectionGeneratorWithGroup {...props} />
          <ShowOrSendStateButton
            setShowOrUpload={setShowOrUpload}
            ShowOrUpload={ShowOrUpload}
            StateBank={StateBank}
            languageArray={languageArray}
          />
        </Stack>
      </Container>
    </React.Fragment>
  );
};

export default FileOrPictureSelector;
