import React from "react";
import TextIconStack from "Global/Components/CommonComponents/TextIconStack/TextIconStack";
import { Stack } from "@mui/material";
import { ArabicToPersian } from "utils/ConvertToPersianTextFromArabic";

const InformationOverview = ({ element }) => {
  const cachedIcons = JSON.parse(localStorage.getItem("icons"));
  const imgHeader = "data:image/svg+xml;base64,";

  return (
    <React.Fragment>
      <Stack alignItems="end" marginBottom="1em" spacing={1}>
        <TextIconStack
          Icon={imgHeader + cachedIcons?.GeneralIcon["DocumentTypeIcon.svg"]}
          Gap="1em"
          Text={ArabicToPersian(element?.ItemContent?.nameType)}
        />
        {element?.ItemContent?.expireDate !== "" ? (
          <TextIconStack
            Icon={
              imgHeader + cachedIcons?.GeneralIcon["DateExpiryDocument.svg"]
            }
            Gap="1em"
            Text={element?.expireDate}
          />
        ) : (
          <></>
        )}
        <p>
          {element?.ItemContent?.deaription?.length > 15
            ? element?.ItemContent?.deaription?.substring(0, 15) + "..."
            : element?.ItemContent?.deaription}
        </p>
        <TextIconStack
          Icon={imgHeader + cachedIcons?.GeneralIcon["PictureFileName.svg"]}
          Gap="1em"
          Text={
            ArabicToPersian(element?.ItemContent?.fileName).length > 15
              ? ArabicToPersian(element?.ItemContent?.fileName).substring(
                  0,
                  15
                ) + "..."
              : ArabicToPersian(element?.ItemContent?.fileName)
          }
        />
      </Stack>
    </React.Fragment>
  );
};

export default InformationOverview;
