import { Container, Stack } from "@mui/material";
import React from "react";
import ContentViewer from "./components/ImageViewer/ContentViewer";

const DocumentViewer = ({ contentData, RadioValue, languageArray }) => {
  return (
    <React.Fragment>
      <Container>
        <Stack spacing={2}>
          <ContentViewer
            contentData={contentData}
            RadioValue={RadioValue}
            languageArray={languageArray}
          />
        </Stack>
      </Container>
    </React.Fragment>
  );
};

export default DocumentViewer;
