import React from "react";
import { Add, FindInPage, Home } from "@mui/icons-material";
import { Button } from "@mui/material";
import FindLangKey from "Global/Functions/FindLangKey/FindLangKey";
import AccessControl from "Global/security/AccessControl";

const ShowOrSendStateButton = ({
  setShowOrUpload,
  ShowOrUpload,
  StateBank,
  languageArray,
}) => {
  const btnText = FindLangKey(
    !ShowOrUpload
      ? "upload-oncall-documents-btn-send-documents"
      : "upload-oncall-documents-btn-show-documents",
    languageArray,
    ShowOrUpload
  );
  return (
    <React.Fragment>
      <Button
        disabled={
          !StateBank?.gatheredData ||
          StateBank?.loadingState ||
          AccessControl("send-doctor-document-btn-send")
        }
        fullWidth
        variant="outlined"
        color="primary"
        onClick={() => setShowOrUpload((prev) => !prev)}
        startIcon={!ShowOrUpload ? <Add /> : <FindInPage />}
      >
        {btnText}
      </Button>
    </React.Fragment>
  );
};

export default ShowOrSendStateButton;
