import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import Cookies from 'js-cookie';
import { env } from 'env';

import { ArabicToPersian } from 'utils/ConvertToPersianTextFromArabic';
import { ReturnItemValuePairs } from 'utils/ReturnItemValuePairs';
import { Post } from 'Xhr/Xhr';

export const GetDocumentType = async (setGatheredData) => {
  Post(
    'POST',
    `${process.env.REACT_APP_API_URL_API_DOCTOR_GET_MADRAK_TYPE_LIST}`,
    true,
    {
      clause: '',
    }
  ).then((response) => {
    if (response.data.resCode === 1) {
      setGatheredData(
        response?.data?.Data?.list?.map((element) => {
          return {
            ItemValue: element?.id,
            ItemContent: ArabicToPersian(element?.name),
          };
        })
      );
    } else
      SwalAlert('error', response.data.resMessage, false);
  });
};
