import { Card, Stack } from "@mui/material";
import React, { useContext } from "react";
import InformationOverview from "../InformationOverview/InformationOverview";
import ResponsiveContentInitializer from "./components/ResponsiveContentInitializer";
import ImageDownloader from "./components/ImageDownloader";
import { GlobalContext } from "Global/Context/GlobalContextProvider";

const ContentViewer = ({ contentData, RadioValue, languageArray }) => {
  const GlobalConstants = useContext(GlobalContext);

  return (
    <React.Fragment>
      {contentData?.map((element) => (
        <Card sx={{ p: ".2em" }} key={GlobalConstants.uuidV4()}>
          <InformationOverview element={element} />

          {RadioValue === "picture" ? (
            <Stack alignItems={"center"}>
              <ResponsiveContentInitializer
                element={element}
                RadioValue={RadioValue}
                GlobalConstants={GlobalConstants}
              />
              <ImageDownloader
                element={element?.ItemContent}
                languageArray={languageArray}
              />
            </Stack>
          ) : (
            <Stack direction="row" gap=".5em">
              <ResponsiveContentInitializer
                element={element}
                RadioValue={RadioValue}
                GlobalConstants={GlobalConstants}
              />
              <ImageDownloader
                element={element?.ItemContent}
                languageArray={languageArray}
              />
            </Stack>
          )}
        </Card>
      ))}
    </React.Fragment>
  );
};

export default ContentViewer;
