import { UploadDoctorDocument } from "../Adapters/UploadDoctorDocument";

export const SubmitOnCallUpload = (data, StateBank) => {
  if (!data?.HasExpireDate) data.expireDate = "";
  data.imageOrFile = data.base64Content.fileContent.includes("image")
    ? true
    : false;
  data.fileName = data.base64Content.fileName;
  data.base64Content = data.base64Content.fileContent.replace(
    /^data:(image|application)\/(pdf|.+);base64,/,
    ""
  );
  UploadDoctorDocument(data, null, StateBank);
};
