import { Download, Visibility } from "@mui/icons-material";
import { LinearProgress, Button } from "@mui/material";
import FindLangKey from "Global/Functions/FindLangKey/FindLangKey";
import React from "react";
import { ToBlob } from "utils/ToBlob";

const ResponsiveContentInitializer = ({ element, RadioValue }) => {
  const show_preview_msg = FindLangKey("general-public-preview");

  return (
    <React.Fragment>
      {RadioValue === "picture" ? (
        <img
          src={`data:image/*;base64,${element?.ItemContent?.imageArrayContent?.image}`}
          alt={element?.ItemContent?.nameType}
          style={{
            maxWidth: "50%",
            maxHeight: "50%",
            display: "block",
            borderRadius: "5px",
          }}
        />
      ) : element?.ItemContent?.base64Extention !== "data:image/*" ? (
        <Button
          fullWidth
          variant="outlined"
          sx={{ mt: "1em" }}
          onClick={() => {
            // window.open(
            //   `data:application/pdf;base64,${element?.ItemContent?.imageArrayContent?.image}`
            // );
            window.open(
              ToBlob(
                `${element?.ItemContent?.base64Extention}${element?.ItemContent?.imageArrayContent?.image}`
              ),
              "_blank"
            );
          }}
        >
          {show_preview_msg}
        </Button>
      ) : (
        <></>
      )}
    </React.Fragment>
  );
};

export default ResponsiveContentInitializer;
