import React from "react";
import { GetOnCallDocuments } from "../Adapters/GetOnCallDocuments";
import useGatherDataFromApi from "Global/Hooks/useGatherDataFromApi";

const useStateBank = () => {
  const [forceRender, setForceRender] = React.useState(false);
  const [RadioValue, setRadioValue] = React.useState();
  const [StateBank, setStateBank] = React.useState();
  const [FunctionArray, setFunctionArray] = React.useState([]);
  const [loadingState, setLoadingState] = React.useState(true);
  React.useEffect(() => {
    setFunctionArray([
      {
        functions: GetOnCallDocuments,
        parameters: {
          fileReqParam: RadioValue,
          setLoadingState,
        },
      },
    ]);
  }, [RadioValue]);
  const { gatheredData } = useGatherDataFromApi(FunctionArray, forceRender);

  React.useMemo(() => {
    setStateBank((prev) => ({
      ...prev,
      gatheredData,
      setRadioValue,
      RadioValue,
      setForceRender,
      loadingState,
    }));
  }, [JSON.stringify(gatheredData), loadingState]);
  return StateBank;
};

export default useStateBank;
