import { Download } from "@mui/icons-material";
import { Button } from "@mui/material";
import FindLangKey from "Global/Functions/FindLangKey/FindLangKey";
import React from "react";
import { Base64Downloader } from "utils/Base64Downloader";

const ImageDownloader = ({ element, languageArray }) => {
  return (
    <React.Fragment>
      <Button
        fullWidth
        variant="outlined"
        sx={{ mt: "1em" }}
        onClick={() => {
          Base64Downloader(
            `${element.base64Extention}${element.imageArrayContent?.image}`,
            element.fileName
          );
        }}
      >
        {FindLangKey("general-public-btn-download", languageArray)}
      </Button>
    </React.Fragment>
  );
};

export default ImageDownloader;
