import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import Cookies from 'js-cookie';
import { ReturnItemValuePairs } from 'utils/ReturnItemValuePairs';
import { Post } from 'Xhr/Xhr';
import { env } from 'env';

export const GetOnCallDocuments = async (
  setOnCallDocument,
  fileReqParam
) => {
  fileReqParam.setLoadingState(true);
  const idPerson = JSON.parse(Cookies.get('user'));

  Post(
    'POST',
    `${process.env.REACT_APP_API_URL_API_GET_MADRAK_LIST}`,
    true,
    {
      idRecord: 0,
      fileType:
        fileReqParam?.fileReqParam !== ''
          ? fileReqParam?.fileReqParam === 'file'
            ? 0
            : 1
          : 1,
      idPerson: idPerson.IdPerson,
      returnFileContent: true,
    }
  ).then((response) => {
    if (response.data.resCode === 1) {
      setOnCallDocument(
        ReturnItemValuePairs(
          response?.data?.Data?.listMadrak,
          'id',
          null
        )
      );

      fileReqParam?.setLoadingState(false);
    } else
      SwalAlert('error', response.data.resMessage, false);
  });
};
