import { Box, Checkbox, Stack } from "@mui/material";
import React from "react";
import ErrorSupportForAllMui from "../ErrorSupportForAllMui/ErrorSupportForAllMui";

const CheckBoxWithText = ({
  onChange,
  errors,
  name,
  label,
  gap,
  direction = "rtl",
  disableRightPadding = true,
  StateLift,
  getInitialValue,
}) => {
  const [UiValue, setUiValue] = React.useState(false);
  React.useEffect(() => {
    if (getInitialValue) {
      onChange(UiValue);
    }
  }, []);
  return (
    <React.Fragment>
      <Stack alignItems="flex-end">
        <Stack
          direction="row"
          spacing={gap ?? 0}
          alignItems="center"
          justifyContent={direction === "rtl" ? "flex-end" : "initial"}
        >
          <p>{label}</p>
          <Checkbox
            color="colorPrimer"
            onChange={(event) => {
              setUiValue(event.target.checked);
              StateLift && StateLift(event.target.checked);
              onChange(event.target.checked);
            }}
            checked={UiValue}
            sx={{ pr: disableRightPadding ? 0 : "", paddingY: 0 }}
          />
        </Stack>
        {errors && !!errors[name] ? (
          <Box sx={{ pb: "1em", pr: 0, mr: "-.4em", mt: "-.4em" }}>
            <ErrorSupportForAllMui label={label} />
          </Box>
        ) : (
          <></>
        )}
      </Stack>
    </React.Fragment>
  );
};

export default CheckBoxWithText;
