import React, { useContext } from "react";
import { Container, LinearProgress, Stack, Alert } from "@mui/material";
import { GlobalContext } from "Global/Context/GlobalContextProvider";
import FileOrPictureSelector from "./Components/FileOrPictureSelector/FileOrPictureSelector";
import DocumentViewer from "./Components/DocumentViewer/DocumentViewer";
import DocumentUploader from "./Components/DocumentUploader/DocumentUploader";
import useStateBank from "./StateBank/useStateBank";
import FindLangKey from "Global/Functions/FindLangKey/FindLangKey";

const UploadOnCallDocs = () => {
  const StateBank = useStateBank();
  const [ShowOrUpload, setShowOrUpload] = React.useState(false);
  const languageArray = useContext(GlobalContext)?.LanguageArray;
  const noData_msg = FindLangKey("general-public-flat-alert-no-data");

  return (
    <React.Fragment>
      <Container maxWidth="md" className="upload-oncall-docs-color-layout">
        {StateBank?.forceRender ? <></> : <></>}
        <Stack marginTop="2em">
          <FileOrPictureSelector
            setShowOrUpload={setShowOrUpload}
            ShowOrUpload={ShowOrUpload}
            setRadioValue={StateBank?.setRadioValue}
            StateBank={StateBank}
            languageArray={languageArray}
          />
          {ShowOrUpload ? (
            <DocumentUploader
              StateBank={StateBank}
              imageData={StateBank?.gatheredData}
              languageArray={languageArray}
            />
          ) : StateBank?.gatheredData === null || StateBank?.loadingState ? (
            <Container>
              <LinearProgress color="colorPrimer" />
            </Container>
          ) : StateBank?.gatheredData?.length === 0 ? (
            <Alert
              severity="warning"
              variant="outlined"
              sx={{ display: "flex", justifyContent: "center", mt: ".5em" }}
            >
              {noData_msg}
            </Alert>
          ) : (
            <div
              style={{
                marginTop: ".5em",
              }}
            >
              <DocumentViewer
                contentData={StateBank?.gatheredData}
                RadioValue={StateBank?.RadioValue}
                languageArray={languageArray}
              />
            </div>
          )}
        </Stack>
      </Container>
    </React.Fragment>
  );
};

export default UploadOnCallDocs;

